.ContactPage input, textarea {
    width: 100%;
    margin-bottom: .8em;
    padding:.2em;
  }
  
  .ContactPage p {
    width: 100%;
    text-align:left;
    margin-bottom:.3em;
  }
  .ContactPage{
      max-width:50%;
      margin: auto;
      margin-bottom:2em;
  }
  
  .ContactPage input[type=submit] {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: .6em;
    text-decoration: none;
    width:50%;
    margin: auto;
  }
  .ContactPage span{
    text-align: left;

    }
    

    @media only screen and (max-width: 650px) {


      .ContactPage{
        max-width:70%;
        margin: auto;
        margin-bottom: 2em;
    }
    
    }